//  General
body, * {
    font-family: 'Open Sans', sans-serif;
}

body {
    background: #eeeeee;
    padding-top: 50px;
}

h1 {
    text-align: center;
    color: #282828;
    margin-bottom: 20px;
}

// NAVBAR
.navbar-dark {
    color: #9d9d9d;
    background-color: #373a3c;
}
.navbar-dark a {
    color: #e3e3e3;
    text-shadow: none;
}
.navbar-dark a:hover {
    color: #fff;
}
.navbar-dark .navbar-toggle {
    border-color: #eee;
}
.navbar-dark .navbar-toggle:hover {
    background-color: #282828;
}
.navbar-dark .navbar-toggle .icon-bar {
    background-color: #eee;
}
.navbar-dark .navbar-nav>.active>a,
.navbar-dark .navbar-nav>.active>a:focus,
.navbar-dark .navbar-nav>.active>a:hover {
    color: #eee;
    background-color: #373a3c;
}

// MAIN CONTENT
.main-content {
    padding: 40px 15px;
    text-align: center;
    position: relative;
}

// GRAPH
.graph-container {
    width: 100%;
    background: #fff;
    border-radius: 2px;
    margin: 0 auto 40px;
}

.gnode text {
    font-weight: 700;
    font-size: 18px;
    color: #383838;
}

.node {
  stroke: #fff;
  stroke-width: 1.5px;
}

.link {
  stroke: #999;
  stroke-opacity: .6;
}